@import url('https://fonts.googleapis.com/css?family=PT+Sans:700');
@import "normalize";
@import "layout";
@import "header";
@import "services";
@import "comments";
@import "new comment";

body{
  width: 100%;
  font-family: 'Arial', sans-serif;
  font-size: 14px;
  color: #333333;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.chat-container{
  width: 720px;
  min-width: 360px;
}

.line-horizontal{
  width: 100%;
  height: 1px;
  opacity: 0.2;
  background-color: #333333;
}

.line-vertical{
  width: 1px;
  height: 100%;
  opacity: 0.2;
  background-color: #333333;
  position: absolute;
}

@media all and (max-width: 480px){
  .services {
    padding-left: 0;
  }
}





