/* Grid clearfix */
.ba-row:before, .ba-row:after {
  content: " ";
  display: table;
}

.ba-row:after {
  clear: both;
}

.ba-row + .ba-row {
  margin-top: 20px;
}

[class*="ba-width-"] {
  width: 100%;
}

.ba-row > [class*="ba-width-"] {
  float: left;
  padding-left: 20px;
}

.ba-row {
  margin-left: -20px;
}

