.person {
  position: relative;
  padding: 16px 20px 0;
  line-height: 20px;
}

.person__photo {
  position: absolute;
  z-index: 2;
  border-radius: 50%;
}

.person__info {
  padding-left: 130px;
}

.person__name {
  font-size: 16px;
  margin: 8px 0 0;
}

.person__position {
  font-size: 12px;
  color: #808080;
  margin-bottom: 5px;
}

.person__description-of-services {
  margin: 0 0 15px 30px;
  padding: 0 10px 0 100px;
  display: flex;
  align-items: center;
  min-height: 54px;
  box-shadow: 0 0 0 1px rgba(60, 44, 0, 0.25);
  border-radius: 4px 3px 3px 4px;
  background-color: #fffbc8;
}

.services {
  padding-left: 130px;
}

.services__title {
  text-align: right;
  padding-right: 33px;
  font-size: 13px;
  font-weight: 400;
  line-height: 15px;
}

.services__container {
  margin: 15px 0;
  position: relative;
  height: 100%;
}

.services__item {
  display: flex;
  justify-content: space-between;
  padding: 0 43px 0 0;
  position: relative;
}

.services__item-rate {
  height: 24px;
  border-radius: 0 2px 2px 0;
  position: absolute;
  z-index: -1;
}

.services__item-rate--reservation {
  width: 219px;
  background-color: rgba(60, 179, 5, 0.4);
}

.services__item-rate--tour {
  width: 54px;
  background-color: rgba(48, 182, 238, 0.4);
}

.services__item-rate--hotel {
  width: 36px;
  background-color: rgba(48, 182, 238, 0.4);
}

.services__item-name {
  font-size: 13px;
  line-height: 26px;
  padding-left: 7px;
}

.services__item-amount {
  font-size: 13px;
  font-weight: 700;
  line-height: 15px;
}

.services__total {
  display: flex;
  justify-content: space-between;
  padding: 0 43px 0 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  margin: 15px 0 25px;
}

.comments {
  padding: 0 20px;
}

.comments__common {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.comments__common-rate {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.comments__common-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.comments__common-header {
  font-size: 16px;
  font-weight: 700;
  margin: 0;
  padding-right: 10px;
}

.comments__rate-info {
  display: flex;
  align-items: center;
}

.comments__rate-info:first-child {
  margin-right: 20px;
}

.comments__rate-quantity {
  margin-left: 5px;
  font-size: 12px;
}

.comments__list {
  padding-left: 0;
  list-style: none;
}

.comments__item {
  margin-bottom: 20px;
}

.comments__item-person-name {
  font-weight: 700;
  display: inline-block;
  margin: 0 10px 12px 0;
}

.comments__item-data {
  color: #808080;
  font-size: 11px;
  display: inline-block;
}

.comments__item-text {
  padding: 15px 20px;
  display: flex;
  align-items: center;
}

.comments__item-text {
  position: relative;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  border: 1px solid #cfcfcf;
  background-color: #f2fbff;
}

.comments__item-text:after, .comments__item-text:before {
  bottom: 100%;
  left: 24px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.comments__item-text:after {
  border-left-color: #f2fbff;
  border-bottom-color: #f2fbff;
  border-width: 4px;
}

.comments__item-text:before {
  border-left-color: #cfcfcf;
  border-bottom-color: #cfcfcf;
  border-width: 5px;
  margin-left: -1px;
}

.new-comment {
  background-color: #f2f2f2;
  padding: 30px 20px 35px;
}

.new-comment__input {
  width: 100%;
  min-height: 65px;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.44), inset 0 1px 2px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  border: 1px solid #a9a9a9;
  background-color: #ffffff;
  outline: none;
  padding: 10px;
}

.new-comment__add {
  width: 281px;
  height: 42px;
  border-radius: 21px;
  background-color: #fdd639;
  margin: 23px auto 0;
  display: block;
  outline: none;
  border: none;
  font-family: "PT Sans";
  font-size: 16px;
  font-weight: 700;
}

.new-comment__add:hover {
  background-color: rgba(60, 179, 5, 0.4);
}

body {
  width: 100%;
  font-family: 'Arial', sans-serif;
  font-size: 14px;
  color: #333333;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.chat-container {
  width: 720px;
  min-width: 360px;
}

.line-horizontal {
  width: 100%;
  height: 1px;
  opacity: 0.2;
  background-color: #333333;
}

.line-vertical {
  width: 1px;
  height: 100%;
  opacity: 0.2;
  background-color: #333333;
  position: absolute;
}

@media all and (max-width: 480px) {
  .services {
    padding-left: 0;
  }
}
