.comments{
  padding: 0 20px;

  &__common{
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-rate{
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }

    &-info{
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }

    &-header{
      font-size: 16px;
      font-weight: 700;
      margin: 0;
      padding-right: 10px;
    }

  }

  &__rate{
    &-info{
      display: flex;
      align-items: center;

      &:first-child{
        margin-right: 20px;
      }
    }
    &-quantity{
      margin-left: 5px;
      font-size: 12px;
    }
  }

  &__list{
    padding-left: 0;
    list-style: none;
  }

  &__item{

    margin-bottom: 20px;

    &-person-name{
      font-weight: 700;
      display: inline-block;
      margin: 0 10px 12px 0;
    }

    &-data{
      color: #808080;
      font-size: 11px;
      display: inline-block;
    }

    &-text{
      padding: 15px 20px;
      display: flex;
      align-items: center;
    }
  }

}

.comments__item-text {
  position: relative;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  border: 1px solid #cfcfcf;
  background-color: #f2fbff;
}
.comments__item-text:after, .comments__item-text:before {
  bottom: 100%;
  left: 24px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.comments__item-text:after {
  border-left-color: #f2fbff;
  border-bottom-color: #f2fbff;
  border-width: 4px;
}
.comments__item-text:before {
  border-left-color: #cfcfcf;
  border-bottom-color: #cfcfcf;
  border-width: 5px;
  margin-left: -1px;
}