.services{
  padding-left: 130px;

  &__title{
    text-align: right;
    padding-right: 33px;
    font-size: 13px;
    font-weight: 400;
    line-height: 15px;
  }

  &__container{
    margin: 15px 0;
    position: relative;
    height: 100%;
  }

  &__item{
    display: flex;
    justify-content: space-between;
    padding: 0 43px 0 0;
    position: relative;

    &-rate{
      height: 24px;
      border-radius: 0 2px 2px 0;
      position: absolute;
      z-index: -1;

      &--reservation{
        width: 219px;
        background-color: rgba(60, 179, 5, 0.4);
      }

      &--tour{
        width: 54px;
        background-color: rgba(48, 182, 238, 0.4);
      }

      &--hotel{
        width: 36px;
        background-color: rgba(48, 182, 238, 0.4);
      }
    }

    &-name{
      font-size: 13px;
      line-height: 26px;
      padding-left: 7px;
    }

    &-amount{
      font-size: 13px;
      font-weight: 700;
      line-height: 15px;
    }
  }

  &__total{
    display: flex;
    justify-content: space-between;
    padding: 0 43px 0 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    margin: 15px 0 25px;
  }
}
