.person{
  position:relative;
  padding: 16px 20px 0;
  line-height: 20px;

  &__photo{
    position: absolute;
    z-index: 2;
    border-radius: 50%;
  }

  &__info{
    padding-left: 130px;
  }

  &__name{
    font-size: 16px;
    margin: 8px 0 0;

  }

  &__position{
    font-size: 12px;
    color: #808080;
    margin-bottom: 5px;
  }

  &__description-of-services{
    margin: 0 0 15px 30px;
    padding: 0 10px 0 100px;
    display: flex;
    align-items: center;
    min-height: 54px;
    box-shadow: 0 0 0 1px rgba(60, 44, 0, 0.25);
    border-radius: 4px 3px 3px 4px;
    background-color: #fffbc8;
  }



}